

import React, { useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'


import { withRouter, NavLink} from 'react-router-dom';

import {  OpenWatchDialog, LoadBlocks} from '../../actions/common'

import * as _Util from '../../store/Util'

import '../_styles.css'

import loadable from '@loadable/component'

const Icon2 = loadable(() => import('../Icons'))



const useObserveChanges = () => {
  const observeChanges =  useSelector(state => state.observeChanges);
  const dispatch = useDispatch(); 



  return { 
    observeChanges,
  }
}




  














const BlocksComponent = (props) => {
  const {
    observeChanges
  } = useObserveChanges();

  let _state = _Util.getStore();
  let keys = _Util.getGlobalsKeys()
  _state["keys"] = keys;
 

  const [initialize, setInitialize] = useState(false);

  const [widthScreen, setWidthScreen] = useState(null);
   
  let searchHash = window.location.hash.split('?')[1]?window.location.hash.split('?')[1]:null;
 
  const router = _Util.parseQuery(searchHash);
  let _id = router.id?router.id:null;
 

  let outerWidth = _state["outerWidth"];


  const id  = _id || _state["ActiveUser"];
  
  let blockList = _state["depositList"];
  let mt = _Util.sortlist(_state["depositList"],"asc");
  useEffect(() => {  
    if(widthScreen!==outerWidth){
      setWidthScreen(outerWidth);
    } 
    if(!initialize){
      if(id && !_state["ActiveUser"]){
        _Util.updStore("ActiveUser",id);
      }
      if(!_state["depositList"]){
        if(props.location && props.location.pathname ===  "/pays"){
          props.history.push({pathname:"/details", search:'?id='+id})
          window.scrollTo(0,0)
        }
      }
      setInitialize(true);
      setTimeout(()=>window.scrollTo(0,0),350);
    }
  });

  

    return (
      <>
        <div className={`main2View blockList`} >        
          <div className={` _dsplFlx `}>
            <div className={`flxbsc20 pddLeft15`}>
              <NavLink  to={{pathname:"/details", search:'?id='+id}} className="">
                <p className={`_Id_card `}>
                  <Icon2 name={'arrowBack'}/>
                </p>
              </NavLink>
            </div>  
          </div>
          <div className={`lolomo paddblock`}>
            {mt && mt.map(minh=>{             
              let itm = blockList[minh]
              return(
                <ItemBlocksComponent minh={minh} itm={itm} key={_Util.gen12CodeId()} />
              )
            })}
          </div>
        </div>
      </>
    );
  
}  





export default withRouter(BlocksComponent)








const ItemBlocksComponent = (props) => {
  
  const {  itm, minh } = props;
  let hr = _Util.getWeekDate(minh,2020)
return (
      <div className={`_pAylIS`}>
            <div className={`_dsplFlx spaceAround `}>         
                <div className={`_dsplFlx flxbsc50 `}  >
                  <div className={`_dsplFlx`}>
                    <h4>{`${hr}`} </h4>
                  </div>
                </div>
                <div className="flexSpace"/>
                <div className={`alignSelf`} >
                  
                  <p className={` `} >
                    <span>{`$ ${itm && itm.toFixed(2)}`}</span>
                  </p>
                </div>
              </div>
            </div>
            )



}